body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-align: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

a.is-active {
  color: #00acdc;
  position: relative;
}

a.is-active:after {
  content: "";
  color: #00acdc;
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

* {
  box-sizing: border-box;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s linear;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  background-size: contain;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__item-name {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__item-product-code {
  color: #616070;
  align-self: flex-start;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card:hover ~ .card__item-name {
  color: #34568b;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.review {
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-top: 16px;
  margin-bottom: 24px;
  display: flex;
}

.review__stars {
  justify-content: space-between;
  display: flex;
}

.review__stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.review__stars-star:nth-of-type(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.review__stars-star:last-child {
  margin-right: 0;
}

.review__amount {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

.grid-container {
  grid-template-rows: auto;
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .grid-container {
    grid-template-columns: 200px 200px;
  }
}

@media screen and (width >= 768px) {
  .grid-container {
    grid-template-columns: 200px 200px 200px;
  }
}

@media screen and (width >= 1024px) {
  .grid-container {
    grid-template-columns: 200px 200px 200px 200px;
  }
}

